.services {
  padding: 162px 227px;

  &__heading {
    text-align: center;
  }

  &__block {
    display: flex;
    margin-top: 162px;
    justify-content: space-between;

    &_service {
      width: 264px;
      transition: 1s;

      &:hover {
        transform: scale(1.2);
        color: $text-color-blue !important;
      }

      &_image {
        height: 160px;
        width: 160px;
        border-radius: 50%;
        box-shadow: 32px 32px 64px rgba(86, 204, 242, 0.05);
        transition: 1s;
      }

      &_heading {
        font-weight: 900;
        font-size: 20px;
        line-height: 140%;
        margin-bottom: 32px;
        margin-top: 64px;
      }

      &_text {
        color: #334563;
      }
    }
  }
}

@media screen and (max-width: 1280px) {
  .services {
    padding: 130px 180px;
  }
}

@media screen and (max-width: 1024px) {
  .services {
    padding: 81px;

    &__block {
      margin-top: 81px;
    }
  }
}
